*{
  margin: 0;
  padding: 0;
}
body{
  background-color: grey;
}
.main{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.center{
  height: 500px;
  width: 350px;
}
.heading{
  padding: 4px;
  color: white;  
  font-family:'Times New Roman', serif;
  text-transform: uppercase;
}
.input{
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}
.input-form{
  margin-left: 10px;
  width: 70%;
  height: 40px;
  border-radius: 14px;
  border:1px solid black;
  padding-left: 10px;  
  box-shadow: 0px 10px 20px rgba(0,0,0,0.3);
  outline: none;
  font-family: 'Times New Roman', serif;
  font-size: large;
}
.add-btn{
  width:110px;
  height:40px;
  border-radius: 15px;
  border:none;
  background-color:#316fc1;
  color: white;
  font-weight: bolder;
  font-size: larger;
  margin-left: 10px;  
  cursor: pointer;
  box-shadow: 0px 5px 10px rgba(11, 10, 10, 0.3);
  transition: 0.3s;
}
.add-btn:active{
  box-shadow: none;
}

.task-list{
  list-style-type: none;
  border:2px solid rgb(24, 23, 23);
  color:rgb(24, 23, 23);
  padding:3px;
  padding-left: 5px;
  border-radius: 5px; 
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  position: relative;
  margin-top: 10px;
}
.icons{
  position: absolute;
  right: 10px;
}
.icons i{
  margin: 4px;
  font-size: large;
  cursor: pointer;
  transition: 0.1s;   
}
.icons i:hover{
  transform: scale(1.2);  
}
.icon-delete:hover{
  color: red;
}